/* Styles for the header component */
.header {
  background-color: #feb717;
  padding: 10px;
  text-align: center;
  display: flex;
  align-items: center;
}

.header img {
  background: #fff;
  border-radius: 50%;
  padding: 5px;
  margin-right: 10px;
}

.header h1 {
  color: #fff;
  font-size: 1rem;
}

/* Styles for the main note component */
.main_note {
  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

.form_div {
  display: flex;
  flex-direction: column;
  width: 500px;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  margin-top: 10px;
  background-color: #fff;
}

.add_btn {
  display: flex;
  align-items: flex-end;
  justify-content: flex-end;
  margin-top: 10px;
}

button {
  color: #feb717;
  border: none;
  background-color: transparent;
  cursor: pointer;
}

.custom_icon {
  color: #feb717;
  box-shadow: 2px 2px 2px rgba(0, 0, 0, 0.5);
  border-radius: 50%;
}

input,
textarea {
  outline: none;
  border: none;
  margin-bottom: 10px;
  letter-spacing: normal;
  line-height: normal;
  padding: 5px;
}

textarea {
  resize: vertical;
}

/* Styles for the notes container */
.notes_container {
  display: flex;
  flex-wrap: wrap;
  justify-content: flex-start;
  margin: 10px;
}

.note {
  margin: 10px;
  padding: 10px;
  width: 200px;
  box-shadow: 2px 4px 4px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  background-color: #fff;
}

.note p {
  font-size: 15px;
  margin: 0;
}

.delete_icon {
  color: #feb717;
}

.note h5 {
  padding: 0;
  margin: 0;
}

.note p {
  padding: 0;
  margin: 0;
}

.delete_btn {
  border: none;
  border-radius: 50%;
  transition: 0.3s;
  display: flex;
  padding: 5px;
  margin-top: 10px;
  background-color: transparent;
}

.delete_btn:hover {
  cursor: pointer;
  
  background: #fff;
}

.delete_icon:hover{
  color: #fff;
  background-color: #feb717;
  border-radius: 50%;
  padding: 1px;
}
/* Styles for the footer component */
.footer {
  background: transparent;
  padding: 10px;
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  position: fixed;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 20px;
}

.footer p {
  color: #666;
  margin: 0;
}
